import React, { useState, useEffect } from 'react';
import { Container, Row, Col, Image } from 'react-bootstrap';
import { useHistory, useParams } from 'react-router-dom';
import styled from 'styled-components';
import '../scss/Detail.scss';

// Lifecycle Hook Example
// class DetailClass extends React.Component {
//     componentDidMount() {
//         //page Loading Event Develop
//         //Ajax Code
//     }

//     componentWillUnmount() {
//         //page Destroy Event Develop
//     }
// }

// styled 문법
let StyledBox = styled.div`
    padding: 20px;
`;
let StyledTitle = styled.h4`
    font-size: 25px;
    color: ${props => props.color}
`;

const Detail = (props) => {
    let [alert, alertUpdate]= useState(true);
    let [temp, tempUpdate]  = useState('');

    // useEffect Hook (Event : Component Rendering, Component Update)
    useEffect(() => {
        // callback
        let timer   = setTimeout(() => {
            alertUpdate(false);
        }, 2 * 1000);

        return () => { clearTimeout(timer); }   // page destroy clear timer

    }, []);    //[alert] : state 가 변경 시 call < [] 사용 시 에는 최초 로드시에만 처리됨 >

    let {id}    = useParams(); 
    let product = props.shoes.find(item => item.id == id);

    let history = useHistory();

    return (
        <Container>
            <StyledBox>
                <StyledTitle className="red">Detail</StyledTitle>
            </StyledBox>
            {temp}
            {/* <input onChange={(e) => {tempUpdate(e.target.value)}}/> */}
            
            {
                alert ? 
                <div className="my-alert">
                    <p>재고가 얼마 남지 않았습니다.</p>
                </div>
                :
                null
            }
            
            <Row>
                <Col md={6}>
                    <Image src={product.src} width="100%"/>
                </Col>
                <Col md={6}>
                    <h4 className="pt-5">{product.title}</h4>
                    <p>{product.content}</p>
                    <p>{product.price}원</p>

                    <Stock stock={props.stock} updateStock={props.updateStock} id={id}/>

                    <button className="btn btn-danger" onClick={() => {
                        let clickStock = [...props.stock];
                        clickStock[id]  = clickStock[id] - 1;

                        props.updateStock(clickStock);
                    }}>주문하기</button>&nbsp;
                    <button className="btn btn-danger" onClick={() => {history.push('/');}}>뒤로가기</button>
                </Col>
            </Row>
        </Container>
    )
}

const Stock = (props) => {
    return (
        <p>재고 : {props.stock[props.id]}</p>
    );
}

export default Detail;